import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import { LinkContainer } from 'react-router-bootstrap';
import { SignedIn, SignedOut, UserButton, SignInButton, SignUpButton } from '@clerk/clerk-react';
import { Button } from 'react-bootstrap';

function MyNav() {
  return (
    <Navbar expand="lg" className="bg-body-tertiary">
      <Container>
        <LinkContainer to="/">
          <Navbar.Brand>Headline API</Navbar.Brand>
        </LinkContainer>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto">
            <LinkContainer to="/home">
              <Nav.Link>Home</Nav.Link>
            </LinkContainer>
            <LinkContainer to="/documentation">
              <Nav.Link>Documentation</Nav.Link>
            </LinkContainer>
            <LinkContainer to="/pricing">
              <Nav.Link>Pricing</Nav.Link>
            </LinkContainer>
          </Nav>
          <Nav className="ms-auto d-flex align-items-center">
            {/* Show when user is NOT signed in */}
            <SignedOut>
              <LinkContainer to="/signin">
                <Nav.Link>Sign In</Nav.Link>
              </LinkContainer>
              <LinkContainer to="/signup">
                <Nav.Link>Sign Up</Nav.Link>
              </LinkContainer>
            </SignedOut>
            
            {/* Show when user IS signed in */}
            <SignedIn>
              <Nav.Link disabled className="text-muted me-2">Welcome back!</Nav.Link>
              <UserButton 
                appearance={{
                  elements: {
                    avatarBox: "w-8 h-8"
                  }
                }}
                showName={false}
                userProfileProps={{
                  additionalOAuthScopes: {
                    github: ['user:email', 'read:user']
                  },
                  appearance: {
                    elements: {
                      rootBox: "w-full",
                      card: "w-full"
                    }
                  }
                }}
                afterSignOutUrl="/"
              >
                <UserButton.MenuItems>
                  <UserButton.Action
                    label="API Tokens"
                    labelIcon={<span></span>}
                    onClick={() => {
                      // Navigate to API tokens page or open modal
                      window.location.href = '#/api-tokens';
                    }}
                  />
                </UserButton.MenuItems>
              </UserButton>
            </SignedIn>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}

export default MyNav;
